import {
  ChangeDetectionStrategy,
  Component,
  EventEmitter,
  Input,
  OnChanges,
  OnInit,
  Output
} from '@angular/core';
import { FormBuilder, FormControl } from '@angular/forms';
import { TranslateService } from '@ngx-translate/core';
import { Tab } from '@sofico-framework/ui-kit/components/tab';
import {
  GroupDefinition,
  GroupDefinitionWithIdentifiers
} from '@sofico-framework/ui-kit/types';
import { hotSafe } from '@sofico-framework/utils';
import { Changes } from 'ngx-reactivetoolkit';
import { combineLatest, Observable, of, ReplaySubject } from 'rxjs';
import { filter, map, switchMap } from 'rxjs/operators';

@Component({
  selector: 'sof-overview-list-group-tabs',
  changeDetection: ChangeDetectionStrategy.OnPush,
  styleUrls: ['./overview-list-group-filter.component.scss'],
  template: `
    <div class="group-tabs">
      <sof-tabs
        [tc]="tc"
        [tabs]="tabs$ | async"
        [active]="activeTab$ | async"
        (clickedTab)="onClickedTab($event)"
      ></sof-tabs>
    </div>
    <div class="group-list">
      <sof-input-single-select
        [tc]="tc"
        [formControl]="formControl"
        [options]="options$ | async"
        [selectorLabel]="labelFn"
        [selectorValue]="valueFn"
        [clearable]="false"
        (changeValue)="onChangeValueList($event)"
      ></sof-input-single-select>
    </div>
  `
})
export class OverviewListGroupFilterComponent<T> implements OnInit, OnChanges {
  formControl: FormControl = this.fb.control(null);
  selectedGroup$ = new ReplaySubject<GroupDefinition>();

  @Input() tc: string;
  @Input() groupSelector: (T) => string | number;

  @Input() set selectedGroup(value: GroupDefinition) {
    this.selectedGroup$.next(value);
    this.formControl.setValue(value?.id);
  }

  @Input() entities: T[];
  @Input() groupDefinitions: GroupDefinition[] = [];
  @Output() selectGroup = new EventEmitter<GroupDefinition>();

  @Changes('entities') entities$: Observable<T[]>;
  @Changes('groupDefinitions') groupDefinitions$: Observable<GroupDefinition[]>;

  // presentation streams
  tabs$: Observable<Tab[]>;
  activeTab$: Observable<Tab>;
  options$: Observable<any>;

  labelFn = x => `${x.label} (${x.count})`;
  valueFn = x => x.id;

  constructor(
    private translateService: TranslateService,
    private fb: FormBuilder
  ) {}

  ngOnInit(): void {
    // presentation streams
    this.tabs$ = this.getTabs$();
    this.activeTab$ = this.getActiveTab$();
    this.options$ = this.getOptions$();
  }

  ngOnChanges(): void {}

  onChangeValueList(groupDefinitionId: string): void {
    this.selectGroup.emit(
      this.groupDefinitions.find(x => x.id === groupDefinitionId)
    );
  }

  onClickedTab(tab: Tab): void {
    this.selectGroup.emit(this.groupDefinitions.find(x => x.id === tab.id));
    this.selectedGroup$.next(this.groupDefinitions.find(x => x.id === tab.id));
  }

  private getTabs$(): Observable<Tab[]> {
    return combineLatest([
      this.entities$.pipe(filter(v => !!v)),
      this.groupDefinitions$.pipe(filter(v => !!v))
    ]).pipe(
      map(([entities, groupDefinitions]) => {
        return groupDefinitions.map(groupDefinition => ({
          label: groupDefinition.label,
          translation: groupDefinition.translation,
          icon: groupDefinition.icon,
          id: groupDefinition.id,
          count:
            // todo: Remove 'groupIdentifiers' in next major release.
            groupDefinition.hasOwnProperty('groupIdentifiers') ||
            groupDefinition.hasOwnProperty('groupIdentifiersIncl') ||
            groupDefinition.hasOwnProperty('groupIdentifiersExcl')
              ? entities?.filter(item => {
                  const selector = this.groupSelector(item);
                  const groupDefinitionTmp = groupDefinition as GroupDefinitionWithIdentifiers;
                  let groupIdentifiersIncluded: (string | number)[];
                  let groupIdentifiersExcluded: (string | number)[];

                  // todo: Remove 'groupIdentifiers' in next major release.
                  if (groupDefinitionTmp.hasOwnProperty('groupIdentifiers')) {
                    groupIdentifiersIncluded =
                      groupDefinitionTmp.groupIdentifiers;
                  } else {
                    groupIdentifiersIncluded =
                      groupDefinitionTmp.groupIdentifiersIncl;
                    groupIdentifiersExcluded =
                      groupDefinitionTmp.groupIdentifiersExcl;
                  }

                  if (Array.isArray(selector)) {
                    const {
                      groupIdentifiersInclStrategy,
                      groupIdentifiersExclStrategy
                    } = groupDefinitionTmp;
                    const includesFn = (v: string | number) =>
                      selector.includes(v);

                    // If either incl. or excl. is undefined we want to ignore said filter and return true (see: ?? true).
                    // It's only needed for the incl. filters as for excl. !undefined = true.
                    return (
                      ((groupIdentifiersInclStrategy === undefined ||
                      groupIdentifiersInclStrategy === 'or'
                        ? groupIdentifiersIncluded?.some(includesFn)
                        : groupIdentifiersIncluded?.every(includesFn)) ??
                        true) &&
                      (groupIdentifiersExclStrategy === undefined ||
                      groupIdentifiersExclStrategy === 'or'
                        ? !groupIdentifiersExcluded?.some(includesFn)
                        : !groupIdentifiersExcluded?.every(includesFn))
                    );
                  }

                  return (
                    groupIdentifiersIncluded?.includes(selector) &&
                    !groupIdentifiersExcluded?.includes(selector)
                  );
                }).length
              : entities.length
        }));
      }),
      hotSafe()
    );
  }

  private getActiveTab$(): Observable<Tab> {
    return combineLatest([this.selectedGroup$, this.tabs$]).pipe(
      map(
        ([selectedGroup, tabs]) =>
          tabs.find(x => x.id === selectedGroup?.id) || tabs[0]
      )
    );
  }

  private getOptions$(): Observable<any> {
    return combineLatest([
      this.entities$.pipe(filter(v => !!v)),
      this.groupDefinitions$.pipe(filter(v => !!v))
    ]).pipe(
      switchMap(([entities, groupDefinitions]) => {
        const keys = groupDefinitions.map(groupDefinition =>
          groupDefinition.label
            ? `${this.tc}.${groupDefinition.label}`
            : undefined
        );

        return combineLatest(
          keys.map((x, i) =>
            x
              ? this.translateService.stream(x)
              : of(groupDefinitions[i]?.translation)
          )
        ).pipe(
          map(translations =>
            translations
              // add translation
              .map((translation, index) => ({
                ...groupDefinitions[index],
                label: translation
              }))
              // add count (not part of the group identifier type)
              .map(groupDefinition => ({
                ...groupDefinition,
                count: groupDefinition.hasOwnProperty('groupIdentifiers')
                  ? entities?.filter(item =>
                      (groupDefinition as GroupDefinitionWithIdentifiers)?.groupIdentifiers?.includes(
                        this.groupSelector(item)
                      )
                    ).length
                  : entities.length
              }))
          )
        );
      })
    );
  }
}
